.container{
    width:100%;
    height:100vh;
    display:flex;
    justify-content: center;
    align-items: center;
    background-color: black;
   

   
}

.container input{
    
    padding:20px 10px;
    width:300px;
    border-radius: 15px;
    margin-right: 20px;
   

} 
.btn1{
    width:100px;
    background-color: blueviolet;
    border: none;
    outline: none;
    font-weight: 600;
    padding:10px;
    border-radius: 10px;
    color:white;
    position: relative;
    font-size: 20px;
}
.btn2{
    position: absolute;
     background-color: blueviolet;
    border: none;
    outline: none;
    font-weight: 600;
    padding:10px;
    border-radius: 10px;
    color:white;
    margin-top: 60px;
    font-size: 20px;

}
.focus{
    color:red;
}


